.grid-chart {
  display: grid;
  font-size: 10px;
  background: #777;
  padding: 12px 20px 30px 20px;
  border: 2px solid #EEE;
  color: #EEE;

  .grid-node {
    border: 1px solid #CCC;
    position: relative;
    border-radius: 5px;
    border-bottom-left-radius: 0;
    background: #888;
    min-height: 100%;
    height: 100%;
    transition-delay: .2s;
    transition: height .2s ease-in-out;

    .node-label {
      position: absolute;
      left: -1px;
      bottom: -16px;
      text-transform: capitalize;
      font-size: 12px;
      padding: 0px 5px 0px 5px;

      background: inherit;
      border: 1px solid #CCC;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      border-top: none;
    }
    .node-data {
      overflow: hidden;
      padding: 2px 5px;
      height: 95%;
      height: -webkit-fill-available;
    }

    .node-data:hover {
      overflow: unset;
    }

    &:hover {
      /*height: fit-content;*/
      height: 230%; /* makes transition work */
      z-index: 1;
      background: #666;
      color: #FFF;
    }
  }

  .x-axis-node,
  .y-axis-node {
    color: #9DC5F6;

    &:hover {
     color: #9DC5F6;
    }
  }
}
